.task-board {
  align-items: flex-start;
  gap: 2rem;
  padding: 1rem;
  display: flex;
}

.task-column {
  background-color: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 8px;
  flex: 1;
  min-width: 280px;
  padding: 1rem;
}

.task-card {
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 6px;
  margin-bottom: 1rem;
  padding: .75rem;
  box-shadow: 0 1px 3px #0000000d;
}

.task-column h2 {
  text-align: center;
  color: #333;
  margin-bottom: 1rem;
  font-size: 1.2rem;
}
